'use strict';

// MODULES //

import resolveGetter from '@stdlib/array-base-resolve-getter';


// FUNCTIONS //

/**
* Tests whether an object has a specified method.
*
* @private
* @param {Object} obj - input object
* @param {string} method - method name
* @returns {boolean} boolean indicating whether an object has a specified method
*
* @example
* var bool = hasMethod( [], 'map' );
* // returns true
*
* @example
* var bool = hasMethod( [], 'beep' );
* // returns false
*/
function hasMethod( obj, method ) {
	return ( typeof obj[ method ] === 'function' );
}


// MAIN //

/**
* Returns an element from an array.
*
* @param {Collection} x - input array
* @param {integer} index - element index
* @returns {*} array element
*
* @example
* var x = [ 1, 2, 3, 4 ];
*
* var v = at( x, 0 );
* // returns 1
*
* v = at( x, 1 );
* // returns 2
*
* v = at( x, -2 );
* // returns 3
*/
function at( x, index ) {
	var get;
	if ( hasMethod( x, 'at' ) ) {
		return x.at( index );
	}
	if ( index < 0 ) {
		index += x.length;
		if ( index < 0 ) {
			return;
		}
	} else if ( index >= x.length ) {
		return;
	}
	get = resolveGetter( x );
	return get( x, index );
}


// EXPORTS //

export default at;
